<script lang="ts">
  import { browser } from "$app/environment";
  import SlideBorderButton from "$lib/components/SlideBorderButton.svelte";
  import Left from "$lib/images/left.svg";
  import Right from "$lib/images/right.svg";
  import { height, rawWidth, width } from "$lib/state/AppState";
  import { get } from "svelte/store";

  export let p1: string;
  export let p2: string;
  export let name: string;
  export let imgSmall: any;
  export let imgLarge: any;
  export let active: boolean;
  export let url: string = "";
  export let pageLoaded: boolean;
  export let scroll: (e: MouseEvent) => void;

  class SlideState {
    static preloaded = false;
    static changeInfo = false;
    static img = get(rawWidth) >= 670 ? imgLarge : imgSmall;

    public static visit(e: MouseEvent) {
      if (browser && e.target) {
        // @ts-ignore
        window.open(e.target.dataset.url, "_blank");
      }
    }

    public static switchText() {
      SlideState.changeInfo = !SlideState.changeInfo;
    }
  }

  $: {
    SlideState.img = get(rawWidth) >= 670 ? imgLarge : imgSmall;
  }
</script>

<div
  class="project"
  class:is-selected={active && pageLoaded}
  style="height: {$height}; width: {$width};"
>
  <div class="center-stuff" style="height: {$height}; width: {$width};">
    <img src={SlideState.img} alt={name} />
    <div>
      <h2>
        {#each name.split("") as letter, index}
          {#if letter === " "}
            <div>&nbsp;&nbsp;&nbsp;</div>
          {/if}
          {#if letter !== " "}
            <div style="transition-delay: {0.3 + index / 20}s;">{letter}</div>
          {/if}
        {/each}
      </h2>
      <div class="info" class:change-info={SlideState.changeInfo}>
        <p>
          {p1}
          <button on:click={SlideState.switchText.bind(SlideState)}>&nbsp;More Info</button>
        </p>
        <p class={$rawWidth < 957 ? "text-switch" : ""}>
          {p2}
          <button on:click={SlideState.switchText.bind(SlideState)}>&nbsp;Back</button>
        </p>
      </div>
      <div class="slide-buttons">
        <button class="scroller" on:click={scroll} data-direction="left">
          <img alt="View previous project" style="margin-left: 0%; margin-right: 10%;" src={Left} />
        </button>
        {#if !!url}
          <SlideBorderButton
            {url}
            text="VISIT"
            active={active && pageLoaded}
            func={SlideState.visit.bind(SlideState)}
          />
        {/if}
        <button class="scroller" on:click={scroll} data-direction="right">
          <img alt="View next project" style="margin-left: 10%; margin-right: 0%;" src={Right} />
        </button>
      </div>
    </div>
  </div>
</div>

